
export default {
  data() {
    return {
      buttonText: '',
      navigation: false,
      navigationCount: 0,
      scrollTarget: null,
    };
  },
  mounted() {
    const demoEl = this.$el.querySelector('.mcw-demo');
    this.scrollTarget = demoEl;
